import React from 'react';
import { Button, Form } from 'react-bootstrap';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import TextField from '@material-ui/core/TextField';

async function formQuery(type, auth, url, query) {
    const pre_request = {
        method: type,
        headers: {'Authorization': 'Bearer ' + auth, 'Content-Type': 'application/json', 'Accept': 'application/json'}
    };
    let q_response = "";
    await fetch(url + query, pre_request).then((response) => response.json()).then((responseData) => q_response=responseData);
    return q_response;
}

async function getSongsFromAlbum(album_id, date, auth) {
    let songs = [];
    let items = await formQuery("GET", auth, 'https://api.spotify.com/v1/albums/' + album_id + "/tracks", "");

    let i = 0;
    for (let item of items['items']) {
        songs.push({'uri': item['uri'], 'name': item['name'], 'date': date, 'order': i});
        i += 1;
    }
    return songs;
}

async function getTracks(type, auth, url, query) {
    const pre_request = {
        method: type,
        headers: {'Authorization': 'Bearer ' + auth, 'Content-Type': 'application/json'}
    };
    let q_response = "";
    await fetch(url + query, pre_request).then((response) => response.json()).then((responseData) => q_response=responseData['items']);
    let tracks = [];
    for (let i = 0; i < q_response.length; i++) {
        tracks.push({'id': q_response[i.toString()]['id'], 'date': q_response[i.toString()]['release_date']});
    }
    return tracks;
}

function dateToString(str) {
    str = str.split("-");
    str = str.join();
    return parseInt(str, 10);
}

function removeDuplicateSongs(data, remixes) {
    let new_data = [];
    let repeats = 0;
    let num_remixes = 0;
    let songs = {};
    if (remixes) {
        for (let song of data) {
            if (song['name'] in songs) {
                repeats += 1;
            } else {
                new_data.push(song);
                songs[song['name']] = 1
            }
        }
    } else {
        for (let song of data) {
            if (song['name'] in songs) {
                repeats += 1;
            } else {
                if (song['name'].toLowerCase().includes('remix')) {
                     num_remixes += 1;
                } else {
                    new_data.push(song);
                    songs[song['name']] = 1
                }
            }
        }
    }
    console.log('There been ' + repeats + ' songs removed after trimming, ' + num_remixes + ' remixes removed.');
    return new_data
}

function createSingleList(data, key) {
    let string = "";
    for (let track of data) {
        string += track[key];
        string += "\n";
    }
    return string;
}

export class SpotifyAPI extends React.Component {

    constructor(props) {
        super(props);
        this.access_token = "";
        this.artist = "";
        this.state = {
            message: 'Default content',
            copy_value: '',
            count: "There are 0 songs (so far)",
            albums_incl: false,
            singles_incl: false,
            appears_on: false,
            compilation: false,
            remixes: false,
        };
        this.handleInputChange = this.handleInputChange.bind(this);
    }

    async get_tracks(id) {
        if (this.access_token === "")
            await this.get_access();

        let i = 0;
        let albums = [];

        // We want to separate these, as Spotify currently has a bug of returning too many things while everything
        // is selected in included_group
        if (this.state.albums_incl) {
            do  {
                let new_albums = await getTracks("GET", this.access_token, "https://api.spotify.com/v1/artists/" + id + "/albums", "?offset=" + i + "&limit=50&include_groups=album&market=US");
                albums = albums.concat(new_albums);
                i += 1;
            } while (albums.length / (i + 1) === 50)
        }
        let num_albums = albums.length;
        if (this.state.singles_incl) {
            i = 0;
            do  {
                let new_albums = await getTracks("GET", this.access_token, "https://api.spotify.com/v1/artists/" + id + "/albums", "?offset=" + i + "&limit=50&include_groups=single&market=US");
                albums = albums.concat(new_albums);
                i += 1;
            } while ((albums.length - num_albums) / (i + 1) === 50)
        }
        num_albums = albums.length;
        if (this.state.appears_on) {
            i = 0;
            do  {
                let new_albums = await getTracks("GET", this.access_token, "https://api.spotify.com/v1/artists/" + id + "/albums", "?offset=" + i + "&limit=50&include_groups=appears_on&market=US");
                albums = albums.concat(new_albums);
                i += 1;
            } while ((albums.length - num_albums) / (i + 1) === 50)
        }
        num_albums = albums.length;
        if (this.state.compilation) {
            i = 0;
            do  {
                let new_albums = await getTracks("GET", this.access_token, "https://api.spotify.com/v1/artists/" + id + "/albums", "?offset=" + i + "&limit=50&include_groups=compilation&market=US");
                albums = albums.concat(new_albums);
                i += 1;
            } while ((albums.length - num_albums) / (i + 1) === 50)
        }


        let tracks = [];
        // Probably not necessary, but better to be safe
        let max_album_size = albums.length > process.env.REACT_APP_MAX_ALBUMS ? process.env.REACT_APP_MAX_ALBUMS : albums.length;

        // Every song has an album, but we don't know how many songs per album
        for (let j = 0; j < max_album_size; j++) {
            let new_tracks = await getSongsFromAlbum(albums[j]['id'], albums[j]['date'], this.access_token);
            tracks = tracks.concat(new_tracks)
        }

        // Just remove duplicates as best as possible
        tracks = removeDuplicateSongs(tracks, this.state.remixes);

        // Sort by date (the whole point), as well as by album order
        tracks.sort(function(first, second) {
            return  (dateToString(first['date']) + (first['order'] * 0.01)) - (dateToString(second['date']) + (second['order'] * 0.01));
        });

        // Display it, and put it on the copy paste button.
        this.setState({count: "There are " + tracks.length + " songs."});
        this.setState({copy_value: createSingleList(tracks, 'uri')});
        this.setState({message: createSingleList(tracks, 'name')});
    }

    async get_artist(artist) {
        if (this.access_token === "")
            await this.get_access();
        artist = encodeURIComponent(artist.trim());
        let artists = await formQuery("GET", this.access_token, "https://api.spotify.com/v1/search", "?q=" + artist + "&type=artist");
        let artist_id = artists['artists']['items'][0]['id'];
        this.get_tracks(artist_id)
    }

    async get_access() {
        let auth = 'Basic ' + process.env.REACT_APP_AUTH + '=';
        const pre_request = {
            method: "POST",
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                'Authorization': auth,
            }
        };
        pre_request.body = "grant_type=client_credentials";
        let access_token = "";
        await fetch('https://accounts.spotify.com/api/token',
            pre_request).then((response) => response.json()).then((responseData) => {access_token=responseData['access_token']});
        this.access_token = access_token;
    }

    async get_song() {
        if (this.access_token === "")
            await this.get_access();
        let resp = await formQuery('GET', this.access_token, 'https://api.spotify.com/v1/search', '?q=illenium&type=artist');
        this.setState({message: resp})
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    render() {
        return (
            <div>
                <div>{ this.state.count } </div>
                <Form>
                    <Form.Group controlId="formArtistName">
                        <TextField id="artist_id" label="Artist Name"
                                   onChange={()=>{this.artist = document.getElementById('artist_id').value}}
                                    helperText="We currently select the top artist available with that name."/>
                    </Form.Group>
                    <Form.Group controlId="albums_included">
                        <Form.Check type="checkbox" label="Include albums"
                                    name="albums_incl"
                                    checked={this.state.albums_incl}
                                    onChange={this.handleInputChange}/>
                    </Form.Group>
                    <Form.Group controlId="singles_included">
                        <Form.Check type="checkbox" label="Include singles"
                                    name="singles_incl"
                                    checked={this.state.singles_incl}
                                    onChange={this.handleInputChange}/>
                    </Form.Group>
                    <Form.Group controlId="appears_on_included">
                        <Form.Check type="checkbox" label="Include songs they appear on"
                                    name="appears_on"
                                    checked={this.state.appears_on}
                                    onChange={this.handleInputChange}/>
                    </Form.Group>
                    <Form.Group controlId="compilations_included">
                        <Form.Check type="checkbox" label="Include compilations"
                                    checked={this.state.compilation}
                                    name="compilation"
                                    onChange={this.handleInputChange}/>
                    </Form.Group>
                    <Form.Group controlId="include_remixes">
                        <Form.Check type="checkbox" label="Include remixes"
                                    name="remixes"
                                    checked={this.state.remixes}
                                    onChange={this.handleInputChange}/>
                    </Form.Group>
                    <Button variant="primary" onClick={()=> this.get_artist(this.artist)}>
                        Submit
                    </Button>
                </Form>
                <CopyToClipboard text={this.state.copy_value}>
                    <button>Copy to clipboard with button, paste into a new album!</button>
                </CopyToClipboard>
                <div>{ this.state.message } </div>
            </div>
        )
    }
}


